<template>
<!-- 条形进度条 -->
  <div class="progress_bar">
    <div class="progress">
      <div class="bar" :style="styleStr"></div>
    </div>
    <div class="text">{{present}}%</div>
  </div>
</template>
<script>
export default {
  name: "progressBar",
  data() {
    return {
     
    };
  },
  props: {
    present: Number
  },
  computed:{
    styleStr(){
      return   `width:${this.present}%`;;
    }
  },
};
</script>




