<template>
  <!-- 裁剪图片定位 -->
  <div class="cut_image">
    <canvas class="myCanvas" ref="myCanvas"></canvas>
  </div>
</template>
<script>
export default {
  name: "cutImage",
  data() {
    return {
      img: new Image(), //图片
      ctx: "", //canvas 内容
      canvasWidth: 0, //canvas 宽度
      canvasHeight: 0, //canvas高度
      dataUrl: "", //图片地址 file文件
      position: [[0, 0, 0, 0]],
      scaling: 5 //图片缩放比例
      // "https://zhaofan-myichat.oss-cn-beijing.aliyuncs.com/chat/1542871288417.jpg"
    };
  },

  methods: {
    //主页面调用功能
    /**
     *@param imageSrc 本地图片地址file
     *@param position 图片定位array [0,0,10,10]
     */
    showCutImage(imageSrc, position) {
      console.log("showCutImage", imageSrc);
      this.dataUrl = imageSrc;
      this.position = position;

      this.initCanvas();
    },
    //初始化canvas
    initCanvas() {
      let canvas = this.$refs.myCanvas;

      console.log("canvas", canvas, this.dataUrl);
      if (!canvas.getContext) return;
      this.ctx = canvas.getContext("2d");
      const reader = new FileReader();
      reader.readAsDataURL(this.dataUrl);
      let _this = this;
      reader.onload = function(e) {
        //赋值给图片地址
        _this.showImage(canvas, e.target.result);
      };
    },

    //初始化canvas  只有图片地址时 使用
    initCanvas2(imageSrc,position){
      let canvas = this.$refs.myCanvas;
      this.position = position;
      console.log("canvas", canvas, imageSrc);
      if (!canvas.getContext) return;
      this.ctx = canvas.getContext("2d");
      this.showImage(canvas,imageSrc);
    },
    //显示图片
    showImage(canvas, imageSrc) {
      this.img.src = imageSrc; //赋值给图片地址
      this.getScaling(this.img.width, this.img.height);
      this.canvasWidth = Math.round(this.img.width / this.scaling);
      this.canvasHeight = Math.round(this.img.height / this.scaling);
      console.log(
        "img",
        this.img.width,
        this.img.height,
        this.canvasWidth,
        this.canvasHeight,
        this.scaling
      );
      canvas.setAttribute("width", this.canvasWidth);
      canvas.setAttribute("height", this.canvasHeight);
      this.img.onload = this.drawImage();
    },
    //计算图像缩放比
    getScaling(imgWidth, imgHeight) {
      let maxWidth = 350,
        maxHeight = 350;
      //宽高比
      let rate = (imgWidth / imgHeight).toFixed(2);
      if (rate >= 1) {
        this.scaling = (imgWidth / maxWidth).toFixed(1);
      } else {
        this.scaling = (imgHeight / maxHeight).toFixed(1);
      }
    },
    //画图
    drawImage() {
      // draw the image on the canvas
      this.ctx.drawImage(this.img, 0, 0, this.canvasWidth, this.canvasHeight);

      // darken the image with a 50% black fill
      this.ctx.save();
      this.ctx.globalAlpha = 0.5;
      this.ctx.fillStyle = "#2E2E2E";
      this.ctx.fillRect(0, 0, this.canvasWidth, this.canvasHeight);
      this.ctx.restore();
      for (let i = 0; i < this.position.length; i++) {
        let data = this.position[i];
        if (data.length == 4) {
          let x = Math.round(data[0] / this.scaling);
          let y = Math.round(data[1] / this.scaling);
          let width = Math.round(data[2] / this.scaling);
          let height = Math.round(data[3] / this.scaling);
          this.positioning(x, y, width, height);
        }
      }
    },
    //画图定位显示
    positioning(x, y, width, height) {
      // ctx.clip() the area to highlight
      // and redraw the whole image
      // (the image will draw only in the clipping region)
      console.log('xywh',x,y,width,height);
      this.ctx.save();
      this.ctx.beginPath();
      this.ctx.clearRect(x, y, width, height);
      this.ctx.rect(x, y, width, height);
      this.ctx.clip();
      this.ctx.drawImage(this.img, 0, 0, this.canvasWidth, this.canvasHeight);
      this.ctx.restore();
    }
  }
};
</script>

<style>
</style>



