<template>
  <!-- 详情页面top菜单 -->
  <div class="hederLink">
    <ul>
      <li :class="curreyRouterName==routekey[0]? 'activeSpan':''">
        <span @click="goto(routekey[0])">{{$t('main.request')}}</span>
      </li>
      <template v-if="router1Arr.length==0">
        <li :class="curreyRouterName== router1Arr[0]? 'activeSpan':''">
          <span @click="goto(router1Arr[0])">{{$t('main.history')}}</span>
        </li>
      </template>
      <template v-else>
        <li
          :class="curreyRouterName== router1Arr[0] || curreyRouterName== router1Arr[1]? 'activeSpan':''"
        >
          <span @click="goto(router1Arr[0])">{{$t('main.history')}}</span>
        </li>
      </template>
    </ul>
  </div>
</template>
<script>
export default {
  name: "submenu",
  data() {
    return {
      router1Arr: [] //routekey[1] 转换为数组
    };
  },
  props: {
    routekey: Array
  },
  computed: {
    curreyRouterName() {
      return this.$route.name;
    }
  },
  methods: {
    goto(name) {
      this.$router.push({ name: name });
      this.setPageOne();
    },
    //处理routekey[1]  格式"history;detail" 或者"history"
    makefomatroutekey() {
      this.router1Arr = this.routekey[1].split(";");
    },
    // 打开history时 page置1
    setPageOne(){
      this.$store.dispatch("changePage", 1);
    }
  },
  created() {
    
    this.makefomatroutekey();
  }
};
</script>




